import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";

import module from "./style.module.scss";

import firstSvg from "./assets/1.svg";
import secondSvg from "./assets/2.svg";
import thirdSvg from "./assets/3.svg";
import fourthSvg from "./assets/4.png";
import fifthSvg from "./assets/5.svg";
import sixthSvg from "./assets/6.svg";
import seventhSvg from "./assets/7.svg";

function CategoriesSlider() {
  const swiperData = [
    {
      title: "",
      link: "",
      img: firstSvg,
    },
    {
      title: "",
      link: "",
      img: secondSvg,
    },
    {
      title: "",
      link: "",
      img: thirdSvg,
    },
    {
      title: " ",
      link: "",
      img: fourthSvg,
    },
    {
      title: "",
      link: "",
      img: fifthSvg,
    },
    {
      title: "",
      link: "",
      img: sixthSvg,
    },
    {
      title: "",
      link: "",
      img: seventhSvg,
    },
    {
      title: "",
      link: "",
      img: firstSvg,
    },
    {
      title: "",
      link: "",
      img: secondSvg,
    },
    {
      title: "",
      link: "",
      img: thirdSvg,
    },
  ];

  return (
    <Swiper
      breakpoints={{
        1440: {
          slidesPerView: 10,
          spaceBetween: 15,
        },
        280: {
          slidesPerView: "auto",
        },
      }}

      className={module.categories}
    >
      {swiperData.map((i, index) => (
        <SwiperSlide key={index} className={module.category}>
          <img src={i.img} alt={i.img} />
          <p>{i.title}</p>
        </SwiperSlide>
      ))}
    </Swiper>
  );
}

export default CategoriesSlider;
